import { BigNumber } from '@ethersproject/bignumber';
import unrevealed from '../assets/unrevealed.png';

export enum CharacterTrait {
  Stamina = 1,
  Strength = 2,
  Speed = 3,
  Courage = 4,
  Intelligence = 5
}

export interface INftAttribute {
  id: number;
  display_type: string;
  trait_type: string;
  value: any;
}

export interface INftStats {
  name: string;
  value: number;
}

export interface INftDetails {
  symbol: string;
  index: number;
  attributes: INftAttribute[];
  stats: INftStats[];
  description: string;
  image: string;
  revealed: boolean;
  name: string;
}

function fileExists(image_url: string) {
  var http = new XMLHttpRequest();

  http.open('HEAD', image_url, false);
  http.send();

  return http.status !== 404;
}

export async function loadNftDetailsFromUri(
  index: number,
  tokenId: BigNumber,
  tokenUri: string,
  traits: any
): Promise<INftDetails> {
  const meta = tokenUri.replace('https://farmland-game.github.io/assets/', './assets/');
  const revealed = fileExists(meta);

  var nft: INftDetails;
  if (revealed) {
    const tokenResponse = await fetch(meta);
    nft = (await tokenResponse.json()) as INftDetails;
    nft.image = nft.image.replace('https://farmland-game.github.io/assets/', './assets/');
    nft.revealed = true;
    nft.index = index;
    nft.stats = [];
    nft.stats.push({ name: 'Stamina', value: traits['trait' + CharacterTrait.Stamina].toNumber() });
    nft.stats.push({ name: 'Strength', value: traits['trait' + CharacterTrait.Strength].toNumber() });
    nft.stats.push({ name: 'Speed', value: traits['trait' + CharacterTrait.Speed].toNumber() });
    nft.stats.push({ name: 'Courage', value: traits['trait' + CharacterTrait.Courage].toNumber() });
    nft.stats.push({ name: 'Intelligence', value: traits['trait' + CharacterTrait.Intelligence].toNumber() });
    var maxAttId = Math.max.apply(
      Math,
      nft.attributes.map(a => {
        return a.id;
      })
    );

    if (traits['trait1'].toNumber() > 94) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat',
        value: 'Tank'
      });
    }

    if (traits['trait2'].toNumber() > 94) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat',
        value: 'Warrior'
      });
    }

    if (traits['trait3'].toNumber() > 94) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat',
        value: 'Lightning'
      });
    }

    if (traits['trait4'].toNumber() > 94) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat',
        value: 'Hero'
      });
    }

    if (traits['trait5'].toNumber() > 94) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat',
        value: 'Genius'
      });
    }
  } else {
    nft = newUnrevealed(index);
  }

  return nft;
}

export function newSampleNftDetails(name: string, img: any, attributes: INftAttribute[], index: number): INftDetails {
  return {
    symbol: 'CHARACTER',
    index: index,
    attributes: attributes,
    stats: [
      {
        name: 'Strength',
        value: Math.floor(Math.random() * 70 + 20)
      },
      {
        name: 'Speed',
        value: attributes.some(a => a.value === 'Lightning') ? 97 : Math.floor(Math.random() * 70 + 20)
      },
      { name: 'Stamina', value: Math.floor(Math.random() * 70 + 20) },
      { name: 'Intelligence', value: Math.floor(Math.random() * 70 + 20) },
      { name: 'Courage', value: Math.floor(Math.random() * 70 + 20) }
    ],
    description:
      attributes.length === 0
        ? 'A common Farmland Character'
        : attributes.length === 1
        ? 'An uncommon Farmland Character'
        : 'A rare Farmland Character',
    image: img,
    revealed: true,
    name: name
  };
}

export function newUnrevealed(index: number): INftDetails {
  return {
    symbol: 'CHARACTER',
    index: index,
    attributes: [{ id: 0, display_type: '', trait_type: 'Unknown', value: 'Unknown' }],
    stats: [
      { name: 'Strength', value: 0 },
      { name: 'Speed', value: 0 },
      { name: 'Stamina', value: 0 },
      { name: 'Intelligence', value: 0 },
      { name: 'Courage', value: 0 }
    ],
    description: 'An unknown Farmland Character',
    image: unrevealed,
    revealed: false,
    name: '??? ???'
  };
}
